<template>
  <div>
    <!-- <van-nav-bar
      title="密码修改"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    /> -->
    <div class="content">
      <p class="logo">
        <!-- <img src="@assets/images/crm.png" /> -->
      </p>

      <van-cell-group>
        <van-field disabled v-model="mobile" label="登录账号" placeholder="" />
        <van-field
          v-model="old_pass"
          label="旧密码"
          type="password"
          placeholder="请输入旧密码"
        />
        <van-field
          v-model="user_pass"
          label="新密码"
          placeholder="请输入新密码"
          type="password"
        />
        <van-field
          v-model="ruser_pass"
          type="password"
          label="重复密码"
          placeholder="请输入再次输入密码"
        />
      </van-cell-group>

      <div class="submit" @click="submitLogin">确认修改</div>
    </div>

  </div>
</template> 
<script>
import { upPws, getUserInfo, jobdayTable } from "@api/user";
import { Icon, Toast, Cell, NavBar, CellGroup, Field } from "vant";
import localStorage from "@utils/store/localStorage";

import { trim } from "@utils";

export default {
  name: "ChangePassword",
  components: {
    [Icon.name]: Icon,
    [Field.name]: Field,
    [Toast.name]: Toast,
    [NavBar.name]: NavBar,
  },
  data: function () {
    return {
      checked: true,

      user_pass: "",
      old_pass: "",
      ruser_pass: "",
      mobile: "",
      show: false,
    };
  },
  mounted: function () {
    getUserInfo()
      .then(({ data }) => {
        this.mobile = data.user.mobile;
      })
      .catch((e) => {
        Toast(e.msg);
      });
  }, 
  methods: { 

    onClickLeft() {
      this.$router.go(-1);
    },

    submitLogin() {
      if (trim(this.old_pass) == "") {
        Toast("请输入旧密码");
      } else if (trim(this.user_pass) == "") {
        Toast("请输入新密码");
      } else if (trim(this.user_pass) != trim(this.ruser_pass)) {
        Toast("两次密码不一致");
      } else {
        upPws({
          user_nickname: this.user_nickname,
          old_pass: this.old_pass,
          user_pass: this.user_pass,
        })
          .then(({ data }) => {
            Toast("修改成功");
            location.reload();
          })
          .catch((e) => {
            Toast(e.msg);
          });
      }
    },
  },
};
</script>
